* Theme Name : Kasa.
* Description : style du font pour ce projet.
* Date: novembre 2021. 
*/
html {
  font-size: 62.5%; }

body {
  font-family: "Roboto", sans-serif, sans-serif;
  font-weight: 500;
  font-size: 1.2rem; }
